<template>
  <div class="box">
    <v-navbar/>
    <div id="box-contents">
      <div id="user-list">
        <!-- 타이틀 바-->
        <div class="d-flex justify-content-between title">
          <span class="my-auto">User list</span>
          <button class="my-auto btn-create" v-b-modal.creat-user-modal>Create</button>
        </div>
        <!--- 테이블 헤더 -->
        <div class="d-flex header">
          <span class="my-auto col-no">No.</span>
          <span class="my-auto col-id">ID</span>
          <span class="my-auto col-id">Type</span>
          <span class="my-auto col-created">Created</span>
          <span class="my-auto col-created">Last login</span>
        </div>
        <b-overlay :show="showOverlay" rounded="sm">
          <div v-for="(user, index) in users" :class="getBorderStyle(index)" :key="index">
            <span class="my-auto col-no">{{index + 1}}</span>
            <span class="my-auto col-id">{{user.id}}</span>
            <span class="my-auto col-id">{{getTypeName(user.type)}}</span>
            <span class="my-auto col-created">{{user.created}}</span>
            <span class="my-auto col-created">{{user.last_login}}</span>
            <button class="image-button mx-5" v-show="user.type !== '1'" v-on:click="selectedUser.id = user.id; selectedUser.type=user.type"  v-b-modal.edit-user-modal>
              <img src="../assets/images/ic-edit.png"/>
            </button>
            <button class="image-button xm-5" v-show="user.type !== '1'" v-on:click="selectedUser.id = user.id; selectedUser.type=user.type"  v-b-modal.delete-user-modal>
              <img src="../assets/images/ic-delete.png"/>
            </button>
          </div>
        </b-overlay>
      </div>
    </div>

<!-- begin - popup for create a new user -->
    <b-modal id="creat-user-modal" centered header-class="p-0 custom-modal-header" footer-class="p-0 custom-modal-footer" dialog-class="custom-modal-dialog" content-class="custom-modal-content">
      <template #modal-header>
        <div class="modal-header">Create a new user</div>
      </template>
      
      <div class="d-block padding-y-24">
        <div class="d-flex p-0">
          <span class="flex-fill label-user-id">ID</span>
          <input v-model="newUser.id" type="text" class="input-user-id"/>
        </div>
        <div class="d-flex p-0 margin-top-24">
          <span class="flex-fill label-user-id">Password</span>
          <input v-model="newUser.pwd1" type="password" class="input-user-id"/>
        </div>
        <div class="d-flex p-0 margin-top-24">
          <span class="flex-fill label-user-id">Confirm password</span>
          <input v-model="newUser.pwd2" type="password" class="input-user-id"/>
        </div>
        <div class="d-flex p-0 margin-top-24">
          <span class="flex-fill label-user-id">Type</span>
          <b-form-select v-model="newUser.type" :options="options" class="input-user-id"></b-form-select>
        </div>
      </div>
      <template #modal-footer>
        <div class="d-flex align-items-stretch modal-footer">
          <button @click="createUser()" class="flex-fill modal-btn create">
            Create
          </button>
          <button @click="cancelCreateUser()" class="flex-fill modal-btn cancel">
            Cancel
          </button>
        </div>
      </template>
    </b-modal>
<!-- end - popup for create a new user -->

<!-- begin - popup for edit user -->
    <b-modal id="edit-user-modal" centered header-class="p-0 custom-modal-header" footer-class="p-0 custom-modal-footer" dialog-class="custom-modal-dialog2" content-class="custom-modal-content">
    <!-- <b-modal id="modal-1" centered header-class="p-0" footer-class="p-0"> -->
      <template #modal-header>
        <div class="modal-header">Edit user type</div>
      </template>
      
      <div class="d-block padding-y-24">
        <div class="d-flex p-0">
          <span class="flex-fill label-user-id">ID</span>
          <div class="div-user-id">{{selectedUser.id}}</div>
        </div>
        <div class="d-flex p-0 margin-top-24">
          <span class="flex-fill label-user-id">Type</span>
          <b-form-select v-model="selectedUser.type" :options="options" class="input-user-id"></b-form-select>
        </div>
      </div>
      <template #modal-footer>
        <div class="d-flex align-items-stretch modal-footer">
          <button @click="editUser()" class="flex-fill modal-btn create">
            Edit
          </button>
          <button @click="cancelCreateUser()" class="flex-fill modal-btn cancel">
            Cancel
          </button>
        </div>
      </template>
    </b-modal>
<!-- end - popup for edit user -->

<!-- begin - popup for delete new user -->
    <b-modal id="delete-user-modal" centered header-class="p-0 custom-modal-header" footer-class="p-0 custom-modal-footer" dialog-class="custom-modal-dialog2" content-class="custom-modal-content">
    <!-- <b-modal id="modal-1" centered header-class="p-0" footer-class="p-0"> -->
      <template #modal-header>
        <div class="modal-header">Remove user</div>
      </template>
      
      <div class="d-block">
        <div class="d-flex justify-content-center">
          <img src="../assets/images/img-alert.png" class="mx-auto"/>
        </div>
        <div class="modal-desc">
          Remove {{selectedUser.id}}
        </div>
      </div>

      <template #modal-footer>
        <div class="d-flex align-items-stretch modal-footer">
          <button @click="deleteUser()" class="flex-fill modal-btn create">
            Remove
          </button>
          <button @click="cancelCreateUser()" class="flex-fill modal-btn cancel">
            Cancel
          </button>
        </div>
      </template>
    </b-modal>
<!-- end - popup for delete user -->
  </div>
</template>

<script>
import Navbar from '../components/Navbar.vue'
import axios from 'axios'

export default {
  name: 'user',
  data() {
    return {
      showOverlay: true,
      users: [],
      newUser: {
        id: '',
        pwd1: '',
        pwd2: '',
        type: '1'
      },
      selectedUser: {
        id: '',
        type: '1',
      },
      options: [
          //  {value: '1', text: 'adminstrator' },
           {value: '2', text: 'manager' },
           {value: '3', text: 'guest' },
        ]
    }
  },
  created() {
    this.users.push({
      id: '',
      type: '0',
      created: '',
      lastLogin: ''
    });
    this.users.push({
      id: '',
      type: '0',
      created: '',
      lastLogin: ''
    });

    this.getUserList();
  },
  destroyed() {
  },
  computed: {

  },
  methods: {
    getTypeName(type) {
      switch(type) {
        case '0':
          return ''
        case '1':
          return 'adminstrator'
        case '2':
          return 'manager'
        case '3':
          return 'guest'
        default:
          return 'unknown'
      }
    },
    getBorderStyle(index) {
      return (index == 0) ? 'd-flex user-info-noborder' : 'd-flex user-info'
    },
    createUser() {
      this.showOverlay = true
      if (!this.newUser.id) {
        // TODO : error
      } else if (this.newUser.pwd1 !==  this.newUser.pwd2) {
        // TODO : error
      } else {
        axios({
          method: 'post',
          url: this.$store.getters.apiServer + '/php/users.php',
          data: {
            sid: this.$store.getters.sessionKey,
            id: this.newUser.id,
            pwd: this.newUser.pwd1,
            type: this.newUser.type
          },
        }).then(resp => {
          console.log(resp)
          if (resp.status == 200 && resp.data.state == 'Success') {
            this.getUserList();
          } else {
            //TODO: error
            this.showOverlay = false
          }
        }).catch(err => {
          console.err(err)
          this.showOverlay = false
        })

        this.newUser.id = ''
        this.newUser.pwd1 = ''
        this.newUser.pwd2 = ''
        this.newUser.type = '1'
        this.$bvModal.hide('creat-user-modal')
      }
    },
    editUser() {
      axios({
        method: 'put',
        url: this.$store.getters.apiServer + '/php/users.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid
        },
        data: {
          id: this.selectedUser.id,
          type: this.selectedUser.type
        },
      }).then(resp => {
        console.log(resp)
        if (resp.status == 200 && resp.data.state == 'Success') {
          this.getUserList();
        } else {
          //TODO: error
          this.showOverlay = false
        }
      }).catch(err => {
        console.err(err)
        this.showOverlay = false
      })

      this.selectedUser.id = ''
      this.selectedUser.type = ''
      this.$bvModal.hide('edit-user-modal')
    },
    deleteUser() {
      axios({
        method: 'delete',
        url: this.$store.getters.apiServer + '/php/users.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid,
          target: this.selectedUser.id
        }
      }).then(resp => {
        console.log(resp)
        if (resp.status == 200 && resp.data.state == 'Success') {
          this.getUserList();
        } else {
          //TODO: error
          this.showOverlay = false
        }
      }).catch(err => {
        console.err(err)
        this.showOverlay = false
      })

      this.selectedUser.id = ''
      this.selectedUser.type = ''
      this.$bvModal.hide('delete-user-modal')
    },
    cancelCreateUser() {
      this.newUser.id = '';
      this.newUser.pwd1 = ''
      this.newUser.pwd2 = ''
      this.newUser.type = '1';
      this.selectedUser.id = ''
      this.selectedUser.type = ''
      this.$bvModal.hide('edit-user-modal')
      this.$bvModal.hide('creat-user-modal')
      this.$bvModal.hide('delete-user-modal')
    },
    getUserList() {
      axios({
        method: 'get',
        url: this.$store.getters.apiServer + '/php/users.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid
        },
      }).then((resp) => {
        if (resp.status == 200 && resp.data.state == 'Success') {
          this.users = resp.data.data
          this.showOverlay = false
        }
      }).catch(err => {
        console.log(err)
        setTimeout(() => {
          this.getUserList()
        }, 1000)
      })
    }
  },
  components: {
    'v-navbar': Navbar
  }
}
</script>

<style scoped lang="scss">
@import '@/scss/user.scss';
</style>